.webmain .con .title h2 {
  background: url("../../../images/sprite_video.gif") 0 0 no-repeat; }

.webmain .video .title h2 {
  width: 145px;
  background-position: 0 -120px; }
.webmain .video .title .group li {
  width: 90px; }
.webmain .video .innergroup {
  display: none; }
  .webmain .video .innergroup.active {
    display: block; }
.webmain .video .inner {
  width: 1230px;
  margin-left: -30px; }
  .webmain .video .inner .colsection {
    float: left;
    width: 380px;
    margin-left: 30px; }
    .webmain .video .inner .colsection .item img, .webmain .video .inner .colsection .item sub {
      width: 380px;
      height: 285px; }
    .webmain .video .inner .colsection .item .text {
      position: absolute;
      width: 380px;
      height: 60px;
      margin-top: -60px;
      color: #fff;
      text-indent: 20px;
      background: url("../../../images/space.png") 0 0 repeat;
      filter: progid:DXImageTransform.Microsoft.gradient(startColorstr=#99000000,endColorstr=#99000000);
      background: rgba(0, 0, 0, 0.6); }
      .webmain .video .inner .colsection .item .text h1 {
        font-size: 14px;
        line-height: 30px; }
      .webmain .video .inner .colsection .item .text h3 {
        font-size: 12px;
        line-height: 30px; }
    .webmain .video .inner .colsection .item sub {
      position: absolute;
      margin-top: -285px;
      background: transparent;
      background: url("../../../images/space.png") 0 0 repeat; }
      .webmain .video .inner .colsection .item sub:hover {
        background: rgba(0, 0, 0, 0.3);
        filter: progid:DXImageTransform.Microsoft.gradient(startColorstr=#4c000000,endColorstr=#4c000000); }
    .webmain .video .inner .colsection li {
      position: relative;
      height: 35px;
      line-height: 35px;
      font-size: 12px; }
      .webmain .video .inner .colsection li h3 {
        display: inline-block; }
      .webmain .video .inner .colsection li span {
        position: absolute;
        right: 0;
        color: #ccc; }
